/* Calendar.css */
.calendar-container {
    display: flex;
    justify-content: center;
    padding: 20px;
    background: #f0f2f5; /* Light background for better contrast */
  }
  
  .calendar {
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 1200px; /* Increased max-width for larger calendar */
    background: #fff;
  }
  
  .calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid #ddd;
    background: #007bff; /* Header background color */
    color: #fff; /* Header text color */
  }
  
  .calendar-header button {
    background: #28a745; /* Button color */
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .calendar-header button:hover {
    background: #218838; /* Darker button color on hover */
  }
  
  .calendar-month-year {
    font-size: 1.8em; /* Increased font size for better readability */
    font-weight: 600;
  }
  
  .calendar-days {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    border-top: 1px solid #ddd;
  }
  
  .calendar-day-name,
  .calendar-day {
    text-align: center;
    padding: 20px; /* Increased padding for better spacing */
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }
  
  .calendar-day-name {
    font-weight: bold;
    color: #007bff; /* Day name color */
    background: #f8f9fa; /* Background for day names */
  }
  
  .calendar-day {
    position: relative;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .calendar-day.today {
    background-color: #e9ecef; /* Highlight today */
    border-radius: 50%;
  }
  
  .calendar-day:hover {
    background-color: #d6d6d6; /* Hover background color */
  }
  
  .event-title {
    background: #17a2b8; /* Event color */
    color: #fff;
    border-radius: 5px;
    padding: 5px 10px;
    margin-top: 5px;
    cursor: pointer;
    font-size: 0.9em;
    transition: background-color 0.3s;
  }
  
  .event-title:hover {
    background: #138496; /* Darker color on hover */
  }
  
  .tooltip {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    background: #333;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    font-size: 0.8em;
    white-space: nowrap;
    z-index: 10;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  }
  
  
  
  